import { useState, useContext } from "react";
import { SetDBCt, MyCharsCt } from "../../../App";
import { Box, AbsoluteBox } from "../../../styledCpns/Layout";
import { Text, Img } from "../../../styledCpns/DataDisplay";
import { Select, IconBtn } from "../../../styledCpns/Inputs";
import { Card } from "../../../styledCpns/Database";
import { StarLine, Confirmation } from "../../CommonCpns";
import StatsTable from "../../Readers/StatsTable";
import { wikia, levels } from "../../../data/misc";
import { deepCopy } from "../../../helpers/misc";

export default function SectionAttr(props) {
  const { charData, char } = props;
  const [removed, setRemoved] = useState(null);
  const { setMyChars } = useContext(SetDBCt);
  const isMobile = window.innerWidth <= 700;
  return (
    <Card className="flex-col relative">
      <AbsoluteBox top="1rem" right="1rem">
        <IconBtn
          className="glow-on-hover"
          icon="fas fa-user-slash"
          variant="negative"
          onClick={() => setRemoved(char.name)}
        />
      </AbsoluteBox>
      <div className="pb-1 flex">
        {isMobile && (
          <Img
            className="mr-3 mb-3"
            width="4.875rem"
            src={wikia + charData.icon + ".png"}
            alt={char.name}
          />
        )}
        <div>
          {!isMobile && (
            <Text variant="h1" color={charData.vision}>
              {char.name}
            </Text>
          )}
          <StarLine rarity={charData.rarity} />
          <div className="ml-1 my-1 flex">
            <Text className="mr-1" variant="h5">
              Level
            </Text>
            <Select
              color={charData.vision}
              fontSize="1.25rem"
              fontWeight={700}
              align="right"
              value={char.level}
              onChange={(e) =>
                setMyChars((prev) => {
                  const level = e.target.value;
                  const maxLv = +level.slice(-2);
                  const newChars = deepCopy(prev);
                  newChars[props.charI].level = level;
                  newChars[props.charI].ascension =
                    maxLv === 20 ? 0 : (maxLv - 30) / 10;
                  return newChars;
                })
              }
            >
              {levels.map((lv, i) => (
                <option key={i}>{lv}</option>
              ))}
            </Select>
          </div>
        </div>
      </div>
      <Box className="grow-1 custom-sb" width="18.75rem" height={0}>
        <StatsTable stats={props.ATTRs} breakdown={true} />
      </Box>
      {removed && (
        <ConfirmRemove
          removed={removed}
          setChosenI={props.setChosenI}
          close={() => setRemoved(null)}
        />
      )}
    </Card>
  );
}

function ConfirmRemove(props) {
  const myChars = useContext(MyCharsCt);
  const { setMyChars, setMyWps, setMyArts } = useContext(SetDBCt);
  return (
    <Confirmation
      message={
        <>
          Remove <b>{props.removed}</b>?
        </>
      }
      confirm={() => {
        const charIndex = myChars.findIndex(
          ({ name }) => name === props.removed
        );
        const { weaponID, artIDs } = myChars[charIndex];
        const ids = [];
        for (const type in artIDs) {
          if (artIDs[type]) ids.push(artIDs[type]);
        }
        if (charIndex >= 0) {
          setMyChars((prev) => {
            const chars = deepCopy(prev);
            chars.splice(charIndex, 1);
            return chars;
          });
          if (charIndex === myChars.length - 1) {
            props.setChosenI((prev) => prev - 1);
          }
          setMyWps((prev) => {
            const wps = deepCopy(prev);
            const wpIndex = wps.findIndex(({ ID }) => ID === weaponID);
            if (wpIndex >= 0) wps[wpIndex].user = null;
            return wps;
          });
          if (ids.length) {
            setMyArts((prev) => {
              const arts = deepCopy(prev);
              for (const id of ids) {
                const artIndex = arts.findIndex(({ ID }) => ID === id);
                if (artIndex >= 0) arts[artIndex].user = null;
              }
              return arts;
            });
          }
        }
      }}
      close={props.close}
    />
  );
}
