import { useState, useContext, useCallback } from "react";
import { MyCharsCt, MyWpsCt, MyArtsCt } from "../../../App";
import { FixedCts } from "../index";
import { AbsoluteBox } from "../../../styledCpns/Layout";
import { Select, IconBtn } from "../../../styledCpns/Inputs";
import { Img, Text } from "../../../styledCpns/DataDisplay";
import { ButtonBar, StarLine, BetaMark } from "../../CommonCpns";
import Picker from "../../Picker";
import { Card, MainSelect } from "../styles";
import TabContent from "./TabContent";
import {
  initSelfMCs,
  initSubWpMCs,
  initParty,
  initElmtMCs,
  parseData,
  getMixedCharList
} from "./helpers";
import { initMonster, getSetupCtrl } from "../helpers";
import { wikia, levels } from "../../../data/misc";

export default function CalcChar(props) {
  const { char, charData, setChar } = props;
  const [tab, setTab] = useState("Attributes");
  const [pickerOn, setPickerOn] = useState(false);
  const charPicker = pickerOn && (
    <CharPicker close={() => setPickerOn(false)} setChar={setChar} />
  );
  if (char === null) {
    return (
      <Card bgColor="cardBg1">
        <ButtonBar
          className="mt-3"
          texts={["Choose a Character"]}
          variants={["positive"]}
          handlers={[() => setPickerOn(true)]}
        />
        {charPicker}
      </Card>
    );
  }
  return (
    <Card className="px-4 py-3 flex-col" bgColor="cardBg1">
      <div className="mt-2 pb-4 flex">
        <div className="mr-3 relative" onClick={() => setPickerOn(true)}>
          <AbsoluteBox top="-0.625rem" left="-0.625rem" zIndex={1}>
            <IconBtn
              className="glow-on-hover"
              icon="fas fa-sync-alt"
              variant="positive"
              fontSize="1.25rem"
            />
          </AbsoluteBox>
          {charData.beta && (
            <AbsoluteBox top="-0.5rem" right="-0.5rem" zIndex={1}>
              <BetaMark />
            </AbsoluteBox>
          )}
          <Img
            className="pointer"
            width="6.25rem"
            src={wikia + charData.icon + ".png"}
            alt={char.name}
            draggable={false}
          />
        </div>
        <div className="overflow-hidden">
          <Text className="ellipsis" variant="h1" color={charData.vision}>
            {char.name}
          </Text>
          <StarLine className="mt-1" rarity={charData.rarity} />
          <div className="mt-1 flex">
            <Text variant="h6" className="mr-1">
              Level
            </Text>
            <Select
              color={charData.vision}
              fontSize="1.125rem"
              fontWeight={700}
              align="right"
              value={char.level}
              onChange={(e) =>
                setChar((prev) => {
                  const level = e.target.value;
                  const maxLv = +level.slice(-2);
                  return {
                    ...prev,
                    level,
                    ascension: maxLv === 20 ? 0 : (maxLv - 30) / 10
                  };
                })
              }
            >
              {levels.map((level) => (
                <option key={level}>{level}</option>
              ))}
            </Select>
          </div>
        </div>
      </div>
      <MainSelect
        tab={tab}
        setTab={setTab}
        options={[
          "Attributes",
          "Weapon",
          "Artifacts",
          "Constellation",
          "Talents"
        ]}
      />
      <TabContent tab={tab} char={char} setChar={setChar} />
      {charPicker}
    </Card>
  );
}

function CharPicker(props) {
  const myChars = useContext(MyCharsCt);
  const myWeapons = useContext(MyWpsCt);
  const myArts = useContext(MyArtsCt);
  const {
    setSelfMCs,
    setWpRack,
    setSubWpMCs,
    setArtChest,
    setParties,
    setElmtMCs,
    setMonster,
    setSetups,
    setStdSetupI,
    setCurSetupI,
    setCmpSetupIs
  } = useContext(FixedCts);

  const pickChar = useCallback(
    (unit) => {
      const { char, wp, art } = parseData(unit, myWeapons, myArts);
      props.setChar(char);
      setSelfMCs([initSelfMCs(char.name)]);
      setWpRack([wp]);
      setSubWpMCs([initSubWpMCs()]);
      setArtChest([art]);
      setParties([initParty()]);
      setElmtMCs([initElmtMCs()]);
      setMonster(initMonster());
      setSetups([getSetupCtrl({})]);
      setCurSetupI(0);
      setStdSetupI(0);
      setCmpSetupIs([0]);
    },
    [myWeapons, myArts]
  );
  return (
    <Picker
      data={getMixedCharList(myChars)}
      dataType="Character"
      close={props.close}
      pick={pickChar}
    />
  );
}
