import styled from "styled-components";
import { Text } from "../../../styledCpns/DataDisplay";
import { Select, Checkbox } from "../../../styledCpns/Inputs";
import { swirlables } from "../../../data/elements";
import { colorCodes } from "../../../data/misc";

export function Section(props) {
  return (
    <div className="pt-2">
      <div className="mb-1 flex align-center">
        {!props.immutable && (
          <Checkbox
            className="mr-2"
            checked={props.checked}
            onChange={props.handleCheck}
          />
        )}
        <Text className="pl-1" fontWeight={700} color="lightGold">
          {props.heading}
        </Text>
      </div>
      <Text>{props.desc}</Text>
      {props.setters}
    </div>
  );
}

export const StyledSetter = styled.div`
  margin: 0.5rem 0.5rem 0 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  :not(:last-child) {
    margin-bottom: 1.25rem;
  }
  .label {
    margin-right: 1rem;
    font-size: 1rem;
    line-height: 1.5;
    text-align: right;
  }
`;

const StyledInput = styled.input`
  padding: 0.25rem 0.5rem;
  width: 4rem;
  border: none;
  border-radius: 0.25rem;
  font-size: 1rem;
  text-align: right;
  outline: none;
  &:focus {
    box-shadow: 0 0 0 3px ${colorCodes.lightGold};
  }
`;

export const StyledSelect = styled((props) => (
  <Select bgColor="white" borderRadius="0.25rem" fontWeight={700} {...props} />
))`
  padding: 0 0.5rem;
`;

export const NoContent = ({ type }) => (
  <Text className="mt-4 mb-3" align="center">
    No {type} found
  </Text>
);

export function ModSetters(props) {
  const { mod, inputs } = props;
  return props.labels.map((label, i) => {
    const type = mod.inputTypes[i];
    let inputCpn;
    if (type === "text") {
      inputCpn = (
        <StyledInput
          type="text"
          value={inputs[i]}
          onChange={(e) => props.handleText(e.target.value, i)}
        />
      );
    } else if (type === "check") {
      inputCpn = (
        <Checkbox
          className="mr-1"
          checked={inputs[i]}
          onChange={() => props.handleCheck(i)}
        />
      );
    } else {
      const isSelect = type === "select"; // or swirl = absorption
      const options = isSelect ? [...Array(mod.maxs[i]).keys()] : swirlables;
      inputCpn = (
        <StyledSelect
          value={inputs[i]}
          onChange={(e) => props.handleSelect(e.target.value, i)}
        >
          {options.map((opt, j) => (
            <option key={j}>{isSelect ? opt + 1 : opt}</option>
          ))}
        </StyledSelect>
      );
    }
    return (
      <StyledSetter key={i}>
        <p className="label">{label}</p>
        {inputCpn}
      </StyledSetter>
    );
  });
}
