import { useState } from "react";
import { Box, ModalCarpet } from "../../../styledCpns/Layout";
import { Button } from "../../../styledCpns/Inputs";
import { Text, ColoredSpan } from "../../../styledCpns/DataDisplay";
import { SetBonus } from "../../CommonCpns";
import { Notes } from "../../Calculator/Modifiers/Target";
import StatsTable from "../../Readers/StatsTable";
import { StatWrapper, ModWrapper } from "./styles";
import Buffs from "./Buffs";
import Debuffs from "./Debuffs";
import { useCloseModal } from "../../../helpers/hooks";

export default function TopBar(props) {
  const [modal, setModal] = useState(null);
  return (
    <Box className="mb-2 flex justify-center">
      {["Stats", "Modifiers"].map((type, i) => (
        <Button
          key={i}
          className="mx-2 glow-on-hover"
          variant="default"
          onClick={() => setModal(type)}
        >
          {type}
        </Button>
      ))}
      {modal ? (
        <Modal
          type={modal}
          result={props.result}
          setup={props.setup}
          close={() => setModal(null)}
        />
      ) : null}
    </Box>
  );
}

function Modal(props) {
  const { type, result } = props;
  const ref = useCloseModal(props.close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="custom-sb white-glow-shadow"
        height="85%"
        borderRadius="0.5rem"
        bgColor="cardBg1"
        style={{ maxWidth: "90%" }}
      >
        {type === "Stats" ? (
          <Stats
            ATTRs={result.ATTRs}
            artSBnes={result.artSBnes}
            sets={props.setup.art.sets}
          />
        ) : (
          <Modifiers setup={props.setup} result={result} />
        )}
      </Box>
    </ModalCarpet>
  );
}

function Stats(props) {
  return (
    <div className="full-h flex">
      <StatWrapper>
        <Text variant="h6" fontWeight={700} color="orange">
          Final Attributes
        </Text>
        <div className="mt-1 hide-sb">
          <StatsTable stats={props.ATTRs} breakdown={true} />
        </div>
      </StatWrapper>
      <StatWrapper>
        <Text variant="h6" fontWeight={700} color="orange">
          Artifact Stats
        </Text>
        <div className="mt-1 hide-sb">
          <StatsTable stats={props.artSBnes} breakdown={false} />
        </div>
      </StatWrapper>
      <StatWrapper>
        <div className="full-h hide-sb">
          <SetBonus sets={props.sets} />
        </div>
      </StatWrapper>
    </div>
  );
}

function Modifiers(props) {
  const { setup } = props;
  const { char, selfMCs, art, elmtMCs, party } = setup;
  const { ATTRs, rxnBnes, infusion } = props.result;
  return (
    <div className="full-h flex">
      <ModWrapper type="BUFFS USED" padding="pl-3 pr-2">
        <Buffs
          char={char}
          selfBCs={selfMCs.BCs}
          weapon={setup.weapon}
          subWpBCs={setup.subWpMCs.BCs}
          arts={art}
          elmtMCs={elmtMCs}
          infusion={infusion}
          party={party}
          ATTRs={ATTRs}
          rxnBnes={rxnBnes}
        />
      </ModWrapper>
      <ModWrapper type="DEBUFFS USED" padding="px-2">
        <Debuffs
          char={char}
          selfDCs={selfMCs.DCs}
          artSubDCs={art.subDCs}
          elmtMCs={elmtMCs}
          party={party}
        />
      </ModWrapper>
      <ModWrapper type="TARGET" padding="pl-2 pr-3">
        <Box className="full-h px-2">
          {Object.entries(setup.target).map((pair, i) => (
            <Text key={i} className="mb-1" variant="h6">
              <ColoredSpan
                className="mr-2"
                color={
                  pair[0] === "Level" ? "lightGold" : pair[0].split(" ")[0]
                }
              >
                {pair[0]}:
              </ColoredSpan>
              <b>{pair[1]}</b>
            </Text>
          ))}
          <div className="mt-4">
            <Notes char={char} infusion={infusion} />
          </div>
        </Box>
      </ModWrapper>
    </div>
  );
}
