import styled from "styled-components";
import { colorCodes } from "../../data/misc";

export const NavBtn = styled.div`
  display: flex;
  align-items: center;
  background-color: ${colorCodes.cardBg3};
  &:hover {
    color: ${colorCodes.lightGold};
  }
  &.current {
    background-color: ${colorCodes.cardBg1};
    color: ${colorCodes.orange};
  }
  &.border {
    border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  }
`;

export const BarsBtn = styled.button`
  width: 2.5rem;
  height: 2.5rem;
  border: none;
  background-color: ${colorCodes.cardBg3};
  color: white;
  &.showing {
    background-color: ${colorCodes.cardBg1};
    color: ${colorCodes.lightGold};
  }
`;

export const StyledInner = styled.div`
  width: 90%;
  max-width: 28rem;
  border-radius: 0.5rem;
  background-color: ${colorCodes.cardBg2};
  font-weight: 700;
  .option {
    padding: 0.5rem 1rem 0.75rem;
    cursor: default;
  }
  .option:hover {
    background-color: ${colorCodes.cardBg1};
  }
`;

export const Divider = styled.div`
  width: 100%;
  border-bottom: 1px solid ${colorCodes.defaultBg};
`;
