import Picker from "../Picker";
import weapons from "../../data/weapons";
import artifacts from "../../data/artifacts";

export function WpPicker(props) {
  const { wpType } = props;
  const filteredData = [];
  for (let name in weapons[wpType]) {
    const { beta, icon, rarity } = weapons[wpType][name];
    filteredData.push({ name, beta, icon, rarity });
  }
  return (
    <Picker
      needMassAdd={props.needMassAdd}
      data={filteredData}
      dataType={wpType}
      close={props.close}
      pick={({ name }) => props.pick(initWeapon({ type: wpType, name }))}
    />
  );
}

// also used in Calculator: CalcChar/helpers & SetupManager/Settings/helpers
// & MyCharacters/index & MyWeapons/Sections
export function initWeapon({ type, name }) {
  if (!name) {
    name = {
      Bow: "Hunter's Bow",
      Catalyst: "Apprentice's Notes",
      Claymore: "Waster Greatsword",
      Polearm: "Beginner's Protector",
      Sword: "Dull Blade"
    }[type];
  }
  return {
    type,
    name,
    level: "1/20",
    refinement: 1
  };
}

export function ArtPicker(props) {
  const { artType } = props;
  const gold = [];
  const purple = [];
  for (const name in artifacts) {
    for (const rarity of artifacts[name].variants) {
      const { icon } = artifacts[name][artType];
      if (rarity === 5) {
        gold.push({ name, icon, rarity });
      } else {
        purple.push({ name, icon, rarity });
      }
    }
  }
  return (
    <Picker
      needMassAdd={props.needMassAdd}
      data={gold.concat(purple)}
      dataType={artType}
      close={props.close}
      pick={({ name, rarity }) =>
        props.pick(initArtPiece({ type: artType, set: name, rarity }))
      }
    />
  );
}

function initArtPiece({ type, set, rarity }) {
  return {
    type,
    set,
    rarity,
    level: 0,
    mainSType: type === "flower" ? "HP" : type === "plume" ? "ATK" : "ATK%",
    subS: [
      {
        type: "DEF",
        val: 0
      },
      {
        type: "DEF%",
        val: 0
      },
      {
        type: "CRIT Rate",
        val: 0
      },
      {
        type: "CRIT DMG",
        val: 0
      }
    ]
  };
}
