import styled from "styled-components";
import { Collapse } from "@material-ui/core";
import { useContext, useState } from "react";
import { TargetCt } from "../index";
import { ModalCarpet } from "../../../styledCpns/Layout";
import { Text } from "../../../styledCpns/DataDisplay";
import { Checkbox } from "../../../styledCpns/Inputs";
import { ButtonBar } from "../../CommonCpns";
import { StyledTable } from "../../CommonCpns/DmgTable";
import { useCloseModal } from "../../../helpers/hooks";
import { colorCodes } from "../../../data/misc";

const StyledDiv = styled.div`
  width: 22.5rem;
  border-radius: 1rem;
  background-color: ${colorCodes.cardBg3};
  .see-more:hover {
    color: ${colorCodes.lightGold};
  }
  .see-more.active {
    color: ${colorCodes.Green};
  }
  .table_wrapper {
    max-width: 18rem;
  }
`;

export default function OverrideOpts(props) {
  const { type, importedSt, close } = props;
  const [ticked, setTicked] = useState([false, false]);
  const [expanded, setExpanded] = useState(-1);
  const { target } = useContext(TargetCt);

  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <StyledDiv ref={ref} className="p-3 relative">
        <div className="py-2">
          <Text variant="h5" align="center">
            We detect difference(s) between the Calculator and this Setup.
            Choose what you want to override.
          </Text>
          <div>
            {["Character's Info.", "Target's Info."].map((text, i) =>
              type >= 300 || type % 10 === i ? (
                <Info
                  key={i}
                  mt={expanded ? "mt-3" : "mt-2"}
                  text={text}
                  checked={ticked[i]}
                  check={() =>
                    setTicked((prev) => {
                      const result = [...prev];
                      result[i] = !result[i];
                      return result;
                    })
                  }
                  isExpanded={expanded === i}
                  toggle={() => setExpanded(expanded === i ? -1 : i)}
                  obj1={i ? target : props.char}
                  obj2={i ? importedSt.target : importedSt.char}
                />
              ) : null
            )}
          </div>
        </div>
        <ButtonBar
          className={expanded === type % 10 ? "mt-2" : "mt-3"}
          texts={["Cancel", "Confirm"]}
          handlers={[
            close,
            () => {
              props.importSt(...ticked);
              close();
            }
          ]}
        />
      </StyledDiv>
    </ModalCarpet>
  );
}

function Info(props) {
  const { isExpanded } = props;
  return (
    <div className={props.mt}>
      <div className="px-5 flex align-center">
        <Checkbox checked={props.checked} onChange={props.check} />
        <Text className="ml-3" variant="h6">
          {props.text}
        </Text>
        <Text
          className={"ml-2 pointer see-more" + (isExpanded ? " active" : "")}
          variant="h6"
          onClick={props.toggle}
        >
          See more
        </Text>
      </div>
      <Collapse in={isExpanded}>
        <div className="pt-2 flex justify-center">
          <div className="table_wrapper">
            <StyledTable>
              <tbody>
                <tr>
                  <th></th>
                  <th className="lightGold-text">Old</th>
                  <th className="lightGold-text">New</th>
                </tr>
                {Object.keys(props.obj1).map((type, i) =>
                  type === "ascension" ? null : (
                    <tr key={i}>
                      <td style={{ textTransform: "capitalize" }}>{type}</td>
                      <td>{props.obj1[type]}</td>
                      <td>{props.obj2[type]}</td>
                    </tr>
                  )
                )}
              </tbody>
            </StyledTable>
          </div>
        </div>
      </Collapse>
    </div>
  );
}
