import styled from "styled-components";
import { Box } from "../../../styledCpns/Layout";
import { Select } from "../../../styledCpns/Inputs";
import { Text, ColoredSpan } from "../../../styledCpns/DataDisplay";
import { pctOf } from "../../../helpers/misc";
import { artSSTypes, SSVals } from "../../../data/artifacts/config";

export default function ArtSubstats(props) {
  const count = { [props.msType]: 1 };
  for (let { type } of props.substats) {
    count[type] = (count[type] || 0) + 1;
  }
  return props.substats.map(({ type, val }, i) =>
    props.mutable ? (
      <Box key={i} className="mt-2 pt-1 flex align-center" bgColor="cardBg2">
        <Select
          color={count[type] === 1 ? "white" : "Red"}
          fontSize="1.0625rem"
          className="appearance-none bg-for-icon white-arrow-bg artSS-select"
          value={type}
          onChange={(e) => props.changeSSType(e.target.value, i)}
        >
          {artSSTypes.map((type) => (
            <option key={type}>{type}</option>
          ))}
        </Select>
        <Text>+</Text>
        <SSVal
          value={val}
          valid={!val || SSVals[type][props.rarity].includes(val)}
          onChange={(e) => props.changeSSVal(e.target.value, i, val)}
        />
        <span>{pctOf(type)}</span>
      </Box>
    ) : (
      <Box
        key={i}
        className={`mt-2 pl-${props.ssSpace} pt-1 flex align-center`}
        bgColor="cardBg2"
      >
        <p className={"mr-" + props.ssSpace}></p>
        <Text>
          <ColoredSpan
            className="mr-1"
            color={count[type] === 1 ? "white" : "Red"}
          >
            {type}
          </ColoredSpan>
          <ColoredSpan
            color={
              !val || SSVals[type][props.rarity].includes(val) ? "green" : "Red"
            }
          >
            +{val}
            {pctOf(type)}
          </ColoredSpan>
        </Text>
      </Box>
    )
  );
}

let SSVal = styled.input`
  position: relative;
  margin-left: 3px;
  padding: 3px 8px 3px 0;
  width: 3.25rem;
  border: none;
  background-color: transparent;
  color: ${(props) => (props.valid ? "white" : "red")};
  font-size: 1.0625rem;
  text-align: right;
  outline: none;
  bottom: ${(props) => (props.valid ? 0 : "-1px")};
`;
