import { useContext } from "react";
import { SelfMCsCt, PartiesCt, FixedCts, SetupCt } from "../../index";
import { Text } from "../../../../styledCpns/DataDisplay";
import { Section, NoContent, ModSetters } from "../CmnCpns";
import characters from "../../../../data/characters";
import { deepCopy } from "../../../../helpers/misc";
import { processNumInp } from "../../../../helpers/processors";

export function SelfDebuffs({ char }) {
  const selfMCs = useContext(SelfMCsCt);
  const parties = useContext(PartiesCt);
  const { setSelfMCs } = useContext(FixedCts);
  const curSetupI = useContext(SetupCt);
  const content = [];

  selfMCs[curSetupI].DCs.forEach((ctrl, dcIndex) => {
    const { activated, index } = ctrl;
    const debuff = characters[char.name].debuffs[index];
    if (debuff.isGranted(char)) {
      content.push(
        <Section
          key={dcIndex}
          checked={activated}
          handleCheck={() => {
            setSelfMCs((prev) => {
              const MCs = deepCopy(prev);
              MCs[curSetupI].DCs[dcIndex].activated = !activated;
              return MCs;
            });
          }}
          heading={debuff.src}
          desc={debuff.desc({
            fromSelf: true,
            char,
            party: parties[curSetupI]
          })}
          // v2.3 - AnemoMC C6 & Venti C6
          setters={
            <SelfSetterSection
              labels={debuff.selfLabels}
              debuff={debuff}
              inputs={ctrl.inputs}
              setSelfMCs={setSelfMCs}
              curSetupI={curSetupI}
              dcIndex={dcIndex}
            />
          }
        />
      );
    }
  });
  return content.length ? content : <NoContent type="debuffs" />;
}

function SelfSetterSection(props) {
  if (!props.debuff.selfLabels) {
    return null;
  }
  const { debuff, inputs, setSelfMCs, curSetupI, dcIndex } = props;
  return (
    <ModSetters
      mod={debuff}
      inputs={inputs}
      labels={debuff.selfLabels}
      handleSelect={(value, i) =>
        setSelfMCs((prev) => {
          const MCs = deepCopy(prev);
          MCs[curSetupI].DCs[dcIndex].inputs[i] = value;
          return MCs;
        })
      }
      handleText={(value, i) =>
        setSelfMCs((prev) => {
          const MCs = deepCopy(prev);
          MCs[curSetupI].DCs[dcIndex].inputs[i] = processNumInp(
            value,
            inputs[i],
            13
          );
          return MCs;
        })
      }
    />
  );
}

export function PartyDebuffs({ char }) {
  const parties = useContext(PartiesCt);
  const curSetupI = useContext(SetupCt);
  const content = [];
  parties[curSetupI].forEach((teammate, tmI) => {
    if (teammate.DCs.length)
      content.push(
        <TeammateDebuffs
          key={tmI}
          teammate={teammate}
          curSetupI={curSetupI}
          tmI={tmI}
          char={char}
        />
      );
  });
  return content.length ? content : <NoContent type="debuffs" />;
}

function TeammateDebuffs(props) {
  const { setParties } = useContext(FixedCts);
  const { teammate, curSetupI, tmI } = props;
  const tmData = characters[teammate.name];
  const subContent = [];

  teammate.DCs.forEach((ctrl, dcIndex) => {
    const { activated, index, inputs } = ctrl;
    const debuff = tmData.debuffs[index];
    subContent.push(
      <Section
        key={dcIndex}
        checked={activated}
        handleCheck={() => {
          setParties((prev) => {
            const newPts = deepCopy(prev);
            newPts[curSetupI][tmI].DCs[dcIndex].activated = !activated;
            return newPts;
          });
        }}
        heading={debuff.src}
        desc={debuff.desc({
          fromSelf: false,
          char: props.char,
          inputs
        })}
        setters={
          <TmSetterSection
            debuff={debuff}
            inputs={inputs}
            setParties={setParties}
            curSetupI={curSetupI}
            tmI={tmI}
            dcIndex={dcIndex}
          />
        }
      />
    );
  });
  return (
    <>
      <Text
        className="pt-2 mb-m1"
        variant="h6"
        color={tmData.vision}
        align="center"
      >
        <strong>{teammate.name.toUpperCase()}</strong>
      </Text>
      {subContent}
    </>
  );
}

function TmSetterSection(props) {
  if (!props.debuff.labels) {
    return null;
  }
  const { debuff, inputs, setParties, curSetupI, tmI, dcIndex } = props;
  return (
    <ModSetters
      mod={debuff}
      inputs={inputs}
      labels={debuff.labels}
      handleSelect={(value, i) =>
        setParties((prev) => {
          const newPts = deepCopy(prev);
          newPts[curSetupI][tmI].DCs[dcIndex].inputs[i] = value;
          return newPts;
        })
      }
      handleText={(value, i) =>
        setParties((prev) => {
          const newPts = deepCopy(prev);
          newPts[curSetupI][tmI].DCs[dcIndex].inputs[i] = processNumInp(
            value,
            inputs[i],
            13
          );
          return newPts;
        })
      }
    />
  );
}
