import { Box } from "../../../../styledCpns/Layout";
import { Text } from "../../../../styledCpns/DataDisplay";
import { InfoSign } from "../../../CommonCpns";
import Thumb from "../../../Readers/Thumb";
import weapons from "../../../../data/weapons";
import artifacts from "../../../../data/artifacts";
import { wikia, artifactIcons, artTypes } from "../../../../data/misc";

export function Weapon(props) {
  const { weapon } = props;
  const { icon, rarity } = weapons[weapon.type][weapon.name];
  return (
    <Box className="p-1" width="33.33%">
      <Thumb
        noDeco={true}
        item={weapon}
        icon={icon}
        rarity={rarity}
        chosen={props.chosen}
        choose={props.choose}
      />
    </Box>
  );
}

export function ArtPiece(props) {
  const { artP } = props;
  const { icon, rarity = 5 } = artifacts[artP.set][artP.type];
  return (
    <Box className="p-1" width="33.33%">
      <Thumb
        noDeco={true}
        item={artP}
        icon={icon}
        rarity={rarity}
        chosen={props.chosen}
        choose={props.choose}
      />
    </Box>
  );
}

export function ArtIcon(props) {
  const artT = artTypes[props.index];
  return (
    <Box className="p-1" width="33%" height="7.75rem">
      <Box
        className="p-3 full-h flex-center pointer glow-on-hover"
        borderRadius="0.25rem"
        bgColor="cardBg3"
        onClick={() => props.setArtInvType(artT)}
      >
        <img
          className="full-w"
          src={wikia + artifactIcons[artT] + ".png"}
          alt=""
        />
      </Box>
    </Box>
  );
}

function Group(props) {
  return (
    <Box
      className={
        `mt-${props.mt} p-2 flex justify-between align-center contain-info_sign` +
        (props.chosen ? " chosen" : "")
      }
      borderRadius="0.5rem"
      bgColor={props.chosen ? "cardBg2" : "transparent"}
      onClick={props.toggle}
    >
      {props.content}
      {props.available && <InfoSign on={props.chosen} />}
    </Box>
  );
}

export function SetBonus(props) {
  const { sets } = props;
  return (
    <Group
      mt={3}
      chosen={props.chosen}
      content={
        <div>
          <Text variant="h6" color="orange" fontWeight={700}>
            Set Bonus
          </Text>
          <div className="mt-1 pl-2">
            {sets.length ? (
              <>
                <Text color="green" fontWeight={700}>
                  {sets[0].name} ({sets[0].bonusLv * 2 + 2})
                </Text>
                {Boolean(sets[1]) && (
                  <Text className="mt-1" color="green" fontWeight={700}>
                    {sets[1].name} (2)
                  </Text>
                )}
              </>
            ) : (
              <Text color="rgb(185, 185, 185)" fontWeight={700}>
                No Set Bonus
              </Text>
            )}
          </div>
        </div>
      }
      toggle={() => {
        if (sets.length) props.toggleHiddenCard("set bonus");
      }}
      available={!!sets.length}
    />
  );
}

export function StatsBonus(props) {
  return (
    <Group
      mt={2}
      chosen={props.chosen}
      content={
        <Text variant="h6" color="orange" fontWeight={700}>
          Artifact Details
        </Text>
      }
      toggle={() => props.toggleHiddenCard("stats bonus")}
      available={true}
    />
  );
}
