import styled from "styled-components";
import { useContext, useState } from "react";
import { Collapse } from "@material-ui/core";
import { TargetCt, FixedCts, ResultCt, SetupCt } from "../../index";
import {
  Text,
  Link,
  CollapseHeading,
  ColoredSpan
} from "../../../../styledCpns/DataDisplay";
import Monster from "./Monster";
import characters from "../../../../data/characters";
import { colorCodes } from "../../../../data/misc";
import { deepCopy } from "../../../../helpers/misc";

export default function Target(props) {
  const { target } = useContext(TargetCt);
  const { setTarget } = useContext(FixedCts);
  const { allInfusion } = useContext(ResultCt);
  const curSetupI = useContext(SetupCt);
  const [dropped, setDropped] = useState(false);
  const resTypes = Object.keys(target).slice(1);

  return (
    <div>
      <div className="mt-2 px-3">
        <Line
          label="Level"
          color="lightGold"
          value={target.Level}
          handleChange={(e) => {
            const { value } = e.target;
            if (!isNaN(value) && value >= 0 && value <= 100) {
              setTarget((prev) => {
                const newTg = deepCopy(prev);
                newTg.Level = Math.round(+value);
                return newTg;
              });
            }
          }}
        />
        <Monster />
      </div>

      <CollapseHeading
        className={dropped ? "active" : ""}
        onClick={() => setDropped(!dropped)}
      >
        Resistances
      </CollapseHeading>
      <Collapse in={dropped}>
        <div className="pt-3 px-3">
          {resTypes.map((type, i) => {
            const elmt = type.split(" ")[0];
            return (
              <Line
                key={i}
                label={type}
                color={elmt === "Physical" ? "White" : elmt}
                value={target[type]}
                handleChange={(e) => {
                  const res = +e.target.value;
                  if (!isNaN(res) && res <= 999) {
                    setTarget((prev) => {
                      const newTg = deepCopy(prev);
                      newTg[type] = Math.round(res);
                      return newTg;
                    });
                  }
                }}
              />
            );
          })}
          <Text>
            You can search for your target's Resistances on{" "}
            <Link
              href="https://genshin-impact.fandom.com/wiki/Resistance#Base_Enemy_Resistances"
              rel="noreferrer"
              target="_blank"
            >
              this page of the Genshin Impact Wiki
            </Link>
            .
          </Text>
        </div>
      </Collapse>
      <Notes char={props.char} infusion={allInfusion[curSetupI]} />
    </div>
  );
}

const Input = styled.input`
  padding: 0.5rem 0.5rem 0.25rem;
  width: 5rem;
  border: none;
  border-radius: 0.25rem;
  text-align: right;
  font-size: 1.125rem;
  &:focus {
    box-shadow: 0 0 0 4px ${colorCodes.red};
  }
`;

function Line(props) {
  return (
    <div className="mb-3 flex justify-between align-center">
      <Text variant="h6" color={props.color}>
        {props.label}
      </Text>
      <Input type="text" value={props.value} onChange={props.handleChange} />
    </div>
  );
}

const destructedTypes = {
  NA: "Normal Attacks",
  CA: "Charged Attacks",
  AS: "Aimed Shot",
  CAS: "Fully-charged Aimed Shot",
  PA: "Plunging Attacks"
};

// also used in MySetups/Setup/Modifiers/index
export function Notes({ char, infusion }) {
  const { vision, weapon } = characters[char.name];
  const notes =
    weapon === "Catalyst"
      ? [
          ["NA", vision],
          ["CA", vision],
          ["PA", vision]
        ]
      : Object.entries(infusion);
  if (weapon === "Bow") {
    notes[1][0] = "AS";
    notes.splice(2, 0, ["CAS", vision]);
  }
  return (
    <div className="mt-2 pr-2">
      <Text variant="h6" color="lightGold">
        Notes:
      </Text>
      {notes.map((pair, i) => (
        <Text key={i} className="mt-1">
          <b>{destructedTypes[pair[0]]}</b> deal{" "}
          <ColoredSpan color={pair[1]}>{pair[1]} DMG</ColoredSpan>.
        </Text>
      ))}
    </div>
  );
}
