import { useContext, useEffect } from "react";
import { SetDBCt } from "../../../../App";
import { SetBonus, ButtonBar } from "../../../CommonCpns";
import { MyWpCard, MyArtCard } from "../../../Readers/MyItemCard";
import StatsTable from "../../../Readers/StatsTable";
import { HiddenCard } from "./styles";
import { artTypes } from "../../../../data/misc";
import { deepCopy } from "../../../../helpers/misc";

export default function HiddenGear(props) {
  const { ownerIndex, opened, setOpened } = props;
  const { pieces, sets } = props.art;
  const style = { maxHeight: props.height + 16 };

  useEffect(() => {
    const index = artTypes.indexOf(opened);
    if (
      (index !== -1 && !pieces[index]) ||
      (opened === "set bonus" && !sets.length)
    ) {
      props.close();
    }
  }, [opened, ownerIndex]);

  let Cpn;
  if (opened === "weapon") {
    Cpn = (
      <Weapon
        style={style}
        weapon={props.weapon}
        setWpInvOn={props.setWpInvOn}
      />
    );
  } else {
    if (opened === "set bonus") {
      Cpn = (
        <HiddenCard className="flex" style={style}>
          <div className="px-1 hide-sb">
            <SetBonus sets={sets} />
          </div>
        </HiddenCard>
      );
    } else if (opened === "stats bonus") {
      Cpn = (
        <ArtAllStats
          style={style}
          artSBnes={props.artSBnes}
          close={() => setOpened(null)}
        />
      );
    } else {
      const index = artTypes.indexOf(opened);
      if (index === -1 || !pieces[index]) {
        return null;
      }
      Cpn = (
        <Artifact
          artP={pieces[index]}
          ownerIndex={ownerIndex}
          setShowing={props.setShowing}
          setOpened={setOpened}
          setArtInvType={props.setArtInvType}
        />
      );
    }
  }
  return <div className="py-2">{Cpn}</div>;
}

function Weapon(props) {
  return (
    <HiddenCard className="flex-col" style={props.style}>
      <div className="px-1 grow-1 hide-sb">
        <MyWpCard weapon={props.weapon} mutable={true} />
      </div>
      <ButtonBar
        className="mt-3"
        texts={["Switch"]}
        variants={["positive"]}
        handlers={[() => props.setWpInvOn(true)]}
      />
    </HiddenCard>
  );
}

function Artifact(props) {
  const { artP, ownerIndex } = props;
  const { setMyChars, setMyArts } = useContext(SetDBCt);
  return (
    <HiddenCard>
      <div className="pb-2 hide-sb">
        <MyArtCard art={artP} mutable={true} />
      </div>
      <ButtonBar
        className="mt-4"
        texts={["Remove", "Switch"]}
        handlers={[
          () => {
            props.setShowing(false);
            setTimeout(() => {
              props.setOpened(null);
              setMyChars((prev) => {
                const newChars = deepCopy(prev);
                newChars[ownerIndex].artIDs[artP.type] = null;
                return newChars;
              });
              setMyArts((prev) => {
                const newArts = deepCopy(prev);
                newArts[
                  newArts.findIndex((art) => art.ID === artP.ID)
                ].user = null;
                return newArts;
              });
            }, 200);
          },
          () => props.setArtInvType(artP.type)
        ]}
      />
    </HiddenCard>
  );
}

function ArtAllStats(props) {
  return (
    <HiddenCard className="flex" style={props.style}>
      <div className="custom-sb">
        <StatsTable stats={props.artSBnes} />
      </div>
    </HiddenCard>
  );
}
