import { useContext } from "react";
import {
  WpRackCt,
  ArtChestCt,
  PartiesCt,
  ResultCt,
  SetupCt,
  FixedCts
} from "../index";
import { SetBonus } from "../../CommonCpns";
import StatsTable from "../../Readers/StatsTable";
import WeaponCard from "../../Readers/WeaponCard";
import ConsList from "../../Readers/ConsList";
import TalentList from "../../Readers/TalentList";
import { StyledList } from "../../Readers/styles";
import { deepCopy } from "../../../helpers/misc";
import { useSwitcher } from "../../../helpers/hooks";

export default function TabContent(props) {
  const { tab, ...others } = props;
  let content;
  if (tab === "Attributes") {
    content = <Attributes />;
  } else if (tab === "Weapon") {
    content = <Weapon />;
  } else if (tab === "Artifacts") {
    content = <Artifacts />;
  } else if (tab === "Constellation") {
    content = <Constellation {...others} />;
  } else {
    content = <Talents {...others} />;
  }
  return <div className="mt-2 pt-1 grow-1 hide-sb">{content}</div>;
}

function Attributes() {
  const { allATTRs } = useContext(ResultCt);
  const curSetupI = useContext(SetupCt);
  return (
    <div className="full-h custom-sb">
      <StatsTable stats={allATTRs[curSetupI]} breakdown={true} />
    </div>
  );
}

function Weapon() {
  const wpRack = useContext(WpRackCt);
  const curSetupI = useContext(SetupCt);
  const { setWpRack } = useContext(FixedCts);
  return (
    <div className="full-h hide-sb">
      <WeaponCard
        weapon={wpRack[curSetupI]}
        mutable={true}
        upgrade={(level) =>
          setWpRack((prev) => {
            const rack = deepCopy(prev);
            rack[curSetupI].level = level;
            return rack;
          })
        }
        refine={(rfm) =>
          setWpRack((prev) => {
            const rack = deepCopy(prev);
            rack[curSetupI].refinement = +rfm;
            return rack;
          })
        }
      />
    </div>
  );
}

function Artifacts() {
  const artChest = useContext(ArtChestCt);
  const { allArtSBnes } = useContext(ResultCt);
  const curSetupI = useContext(SetupCt);
  const [switcher, tab] = useSwitcher([
    { text: "Details", clickable: true },
    { text: "Set Bonus", clickable: true }
  ]);
  return (
    <div className="full-h flex-col">
      <StyledList>
        <div className={"overview " + (tab === "Details" ? "open" : "close")}>
          <div className="full-h custom-sb">
            <StatsTable stats={allArtSBnes[curSetupI]} breakdown={false} />
          </div>
        </div>
        <div className={"details " + (tab === "Set Bonus" ? "open" : "close")}>
          <div className="full-h hide-sb">
            <SetBonus sets={artChest[curSetupI].sets} />
          </div>
        </div>
      </StyledList>
      <div className="mt-3">{switcher}</div>
    </div>
  );
}

function Constellation(props) {
  return (
    <ConsList
      char={props.char}
      handleClick={(i) => {
        props.setChar((prev) => {
          const newChar = deepCopy(prev);
          newChar.constellation = newChar.constellation === i + 1 ? i : i + 1;
          return newChar;
        });
      }}
    />
  );
}

function Talents(props) {
  const parties = useContext(PartiesCt);
  const setupI = useContext(SetupCt);
  return (
    <TalentList
      char={props.char}
      party={parties[setupI]}
      upgradeTl={(tlType, lv) =>
        props.setChar((prev) => {
          const char = deepCopy(prev);
          char[tlType] = lv;
          return char;
        })
      }
    />
  );
}
