import { useState, useEffect } from "react";
import { Text, LGreen, Green, Img } from "../../styledCpns/DataDisplay";
import { Box } from "../../styledCpns/Layout";
import { InfoSign, CloseBtn } from "../CommonCpns";
import { StyledList, SlideShow } from "./styles";
import characters from "../../data/characters";
import { wikia } from "../../data/misc";

export default function PreCheck(props) {
  if (!characters[props.char.name].constellation.length) {
    return (
      <div className="pt-3 px-3">
        <Text variant="h5" align="center">
          The time has not yet come for this person's corner of the night sky to
          light up.
        </Text>
      </div>
    );
  }
  return <ConsList {...props} />;
}

function ConsList(props) {
  const [ctrlLv, setCtrlLv] = useState(0);
  const [atDetails, setAtDetails] = useState(false);
  const { name } = props.char;

  useEffect(() => {
    setAtDetails(false);
  }, [name]);

  return (
    <StyledList>
      <Overview
        charData={characters[name]}
        className={"overview " + (atDetails ? "close" : "open")}
        consLv={props.char.constellation}
        handleClick={props.handleClick}
        toDetails={(i) => {
          setAtDetails(true);
          setCtrlLv(i + 1);
        }}
      />
      <Details
        charData={characters[name]}
        className={"details " + (atDetails ? "open" : "close")}
        ctrlLv={ctrlLv}
        setCtrlLv={setCtrlLv}
        close={() => {
          setAtDetails(false);
          setTimeout(() => setCtrlLv(0), 200);
        }}
      />
    </StyledList>
  );
}

function Overview(props) {
  const { beta, icon, constellation } = props.charData;
  return (
    <div className={props.className}>
      <Box className="full-h hide-sb">
        {constellation.map((cons, i) => {
          const available = props.consLv >= i + 1 ? "" : " unavailable";
          return (
            <div key={i} className={"flex" + (i ? " mt-3" : "")}>
              <div className="flex-center">
                <Img
                  className={"my-1 mr-2 pointer" + available}
                  width="3.25rem"
                  src={wikia + (beta ? icon : cons.image) + ".png"}
                  alt=""
                  draggable={false}
                  onClick={() => props.handleClick(i)}
                />
              </div>
              <div
                className={"grow-1 flex align-center contain-info_sign"}
                onClick={() => props.toDetails(i)}
              >
                <Text
                  className={"px-2" + available}
                  variant="h6"
                  fontWeight={700}
                >
                  {cons.name}
                </Text>
                <div className="ml-auto">
                  <InfoSign />
                </div>
              </div>
            </div>
          );
        })}
      </Box>
    </div>
  );
}

function Details(props) {
  const { beta, icon, vision, constellation } = props.charData;
  const { ctrlLv, setCtrlLv } = props;
  if (!ctrlLv) {
    return <div className="details close" />;
  }
  const { name, desc } = constellation[ctrlLv - 1];

  return (
    <div className={props.className}>
      <div className="full-h flex-col">
        <div className="hide-sb">
          <SlideShow
            index={ctrlLv - 1}
            images={constellation.map((cons) => (beta ? icon : cons.image))}
            back={() => setCtrlLv(ctrlLv - 1)}
            next={() => setCtrlLv(ctrlLv + 1)}
            close={props.close}
          />
          <div className="py-2 cons_details">
            <Text variant="h5" color={vision} fontWeight={700}>
              {name}
            </Text>
            <Text variant="h6">
              Constellation Lv. <Green>{ctrlLv}</Green>
            </Text>
            <Text className="mt-3">
              {ctrlLv === 3 || ctrlLv === 5 ? (
                <>
                  Increases the Level of <LGreen>{desc}</LGreen> by{" "}
                  <Green>3</Green>.<br /> Maximum upgrade level is 15.
                </>
              ) : (
                desc
              )}
            </Text>
          </div>
        </div>
        <div className="mt-3">
          <CloseBtn className="mx-auto glow-on-hover" onClick={props.close} />
        </div>
      </div>
    </div>
  );
}
