import { useEffect, useState, useRef } from "react";
import { Box } from "../../styledCpns/Layout";
import { Text } from "../../styledCpns/DataDisplay";

export function useCloseModal(close) {
  const ref = useRef();
  useEffect(() => {
    const handleClickOutside = (e) => {
      if (ref.current && !ref.current.contains(e.target)) {
        close();
      }
    };
    const handlePressEsc = (e) => {
      if (e.key === "Escape") {
        close();
      }
    };
    document.addEventListener("click", handleClickOutside);
    document.addEventListener("keydown", handlePressEsc);
    return () => {
      document.removeEventListener("click", handleClickOutside);
      document.removeEventListener("keydown", handlePressEsc);
    };
  });
  return ref;
}

export function useGetHeight() {
  const ref = useRef();
  const [height, setHeight] = useState(0);
  useEffect(() => {
    const resizeObserver = new ResizeObserver((entries) => {
      for (let entry of entries) {
        if (entry.contentBoxSize) {
          const contentBoxSize = Array.isArray(entry.contentBoxSize)
            ? entry.contentBoxSize[0]
            : entry.contentBoxSize;
          setHeight(contentBoxSize.blockSize);
        } else {
          setHeight(entry.contentRect.height);
        }
      }
    });
    const elmt = ref.current;
    resizeObserver.observe(elmt);
    return () => resizeObserver.unobserve(elmt);
  }, []);
  return [ref, height];
}

export function useSwitcher(states) {
  const [tab, setTab] = useState(states[0].text);
  const cpn = (
    <div className="full-w flex">
      {states.map(({ text, clickable }, i) => (
        <Box
          key={i}
          className={"py-1 flex-center" + (clickable ? " pointer" : "")}
          bgColor={text === tab ? "defaultBg" : "cardBg3"}
          width="50%"
          borderRadius={i ? "0 1rem 1rem 0" : "1rem 0 0 1rem"}
          onClick={() => {
            if (clickable) setTab(text);
          }}
        >
          <Text color={text === tab ? "black" : "defaultBg"} fontWeight={700}>
            {clickable ? text : ""}
          </Text>
        </Box>
      ))}
    </div>
  );
  return [cpn, tab, setTab];
}
