import { useContext } from "react";
import {
  ElmtMCsCt,
  ArtChestCt,
  ResultCt,
  SetupCt,
  FixedCts
} from "../../index";
import { AmpRxnDesc } from "../../../CommonCpns";
import { StyledSetter, StyledSelect, Section, NoContent } from "../CmnCpns";
import {
  rsnBuffDesc,
  ampElmts,
  crystallizables
} from "../../../../data/elements";
import artifactBuffs from "../../../../data/artifacts/artifactBuffs";
import characters from "../../../../data/characters";
import { deepCopy } from "../../../../helpers/misc";

export function ElmtBuffs({ char }) {
  const elmtMCs = useContext(ElmtMCsCt);
  const { allInfusion } = useContext(ResultCt);
  const curSetupI = useContext(SetupCt);
  const { setElmtMCs } = useContext(FixedCts);
  const content = [];

  elmtMCs[curSetupI].resonance.forEach((rsn, i) => {
    content.push(
      <Section
        key={i}
        checked={rsn.activated}
        handleCheck={() =>
          setElmtMCs((prev) => {
            const MCs = deepCopy(prev);
            MCs[curSetupI].resonance[i].activated = !rsn.activated;
            return MCs;
          })
        }
        heading={rsn.name}
        desc={rsnBuffDesc[rsn.name]}
      />
    );
  });
  const { vision } = characters[char.name];
  if (ampElmts.includes(vision)) {
    content.push(
      {
        Pyro: ["Melt", "Vaporize"].map((rxn) => (
          <RxnBuff key={rxn} rxn={rxn} vision={vision} />
        )),
        Hydro: <RxnBuff key="Vaporize" rxn="Vaporize" vision={vision} />,
        Cryo: <RxnBuff key="Melt" rxn="Melt" vision={vision} />
      }[vision]
    );
  }
  const infusion = allInfusion[curSetupI].NA;
  if (ampElmts.includes(infusion) && infusion !== vision) {
    content.push(
      {
        Pyro: ["Melt", "Vaporize"].map((rxn) => (
          <NaRxnBuff key={rxn} rxn={rxn} infusion={infusion} />
        )),
        Hydro: <NaRxnBuff key="Vaporize" rxn="Vaporize" infusion={infusion} />,
        Cryo: <NaRxnBuff key="Melt" rxn="Melt" infusion={infusion} />
      }[infusion]
    );
  }
  return content.length ? content : <NoContent type="buffs" />;
}

function RxnBuff({ rxn, vision }) {
  const { allRxnBnes } = useContext(ResultCt);
  const elmtMCs = useContext(ElmtMCsCt);
  const curSetupI = useContext(SetupCt);
  const { setElmtMCs } = useContext(FixedCts);
  const activated = elmtMCs[curSetupI].ampRxn === rxn;

  return (
    <Section
      checked={activated}
      handleCheck={() =>
        setElmtMCs((prev) => {
          const MCs = deepCopy(prev);
          MCs[curSetupI].ampRxn = activated ? null : rxn;
          return MCs;
        })
      }
      heading={rxn}
      desc={<AmpRxnDesc elmt={vision} mult={allRxnBnes[curSetupI][rxn]} />}
    />
  );
}

function NaRxnBuff({ rxn, infusion }) {
  const { allRxnBnes } = useContext(ResultCt);
  const elmtMCs = useContext(ElmtMCsCt);
  const curSetupI = useContext(SetupCt);
  const { setElmtMCs } = useContext(FixedCts);
  const activated = elmtMCs[curSetupI].naAmpRxn === rxn;

  return (
    <Section
      checked={activated}
      handleCheck={() =>
        setElmtMCs((prev) => {
          const MCs = deepCopy(prev);
          MCs[curSetupI].naAmpRxn = activated ? null : rxn;
          return MCs;
        })
      }
      heading={rxn + " (external infusion)"}
      desc={
        <AmpRxnDesc elmt={infusion} mult={allRxnBnes[curSetupI]["na" + rxn]} />
      }
    />
  );
}

export function ArtBuffs() {
  const artChest = useContext(ArtChestCt);
  const curSetupI = useContext(SetupCt);
  const { setArtChest } = useContext(FixedCts);
  const { sets, BCs, subBCs } = artChest[curSetupI];
  const content = [];

  const name = sets[0]?.name;
  BCs.forEach((ctrl, bcIndex) => {
    const { activated } = ctrl;
    const buff = artifactBuffs[name][ctrl.index];
    content.push(
      <Section
        key={name + bcIndex}
        checked={activated}
        handleCheck={() =>
          setArtChest((prev) => {
            const newArts = deepCopy(prev);
            newArts[curSetupI].BCs[bcIndex].activated = !activated;
            return newArts;
          })
        }
        heading={name + " (self)"}
        desc={buff.desc}
        setters={
          <SetterSection
            buff={buff}
            inputs={ctrl.inputs}
            handleChange={(inpI, value) =>
              setArtChest((prev) => {
                const newArts = deepCopy(prev);
                newArts[curSetupI].BCs[bcIndex].inputs[inpI] = isNaN(value)
                  ? value
                  : +value;
                return newArts;
              })
            }
          />
        }
      />
    );
  });
  subBCs.forEach((ctrl, bcIndex) => {
    const { set, activated } = ctrl;
    const buff = artifactBuffs[set][ctrl.index];
    content.push(
      <Section
        key={set + bcIndex}
        checked={activated}
        handleCheck={() => {
          setArtChest((prev) => {
            const newArts = deepCopy(prev);
            newArts[curSetupI].subBCs[bcIndex].activated = !activated;
            return newArts;
          });
        }}
        heading={set}
        desc={buff.desc}
        setters={
          <SetterSection
            buff={buff}
            inputs={ctrl.inputs}
            handleChange={(inpI, value) =>
              setArtChest((prev) => {
                const newArts = deepCopy(prev);
                newArts[curSetupI].subBCs[bcIndex].inputs[inpI] = isNaN(value)
                  ? value
                  : +value;
                return newArts;
              })
            }
          />
        }
      />
    );
  });
  return content.length ? content : <NoContent type="buffs" />;
}

function SetterSection(props) {
  if (!props.buff.labels) {
    return null;
  }
  const { buff } = props;
  return buff.labels.map((label, i) => {
    const isStacks = buff.inputTypes[i] === "stacks";
    const options = isStacks
      ? [...Array(buff.maxs[i]).keys()]
      : crystallizables;
    return (
      <StyledSetter key={i}>
        <p className="label">{label}</p>
        <StyledSelect
          value={props.inputs[i]}
          onChange={(e) => props.handleChange(i, e.target.value)}
        >
          {options.map((opt) => (
            <option key={opt}>{isStacks ? opt + 1 : opt}</option>
          ))}
        </StyledSelect>
      </StyledSetter>
    );
  });
}
