import { useContext } from "react";
import { ElmtMCsCt, ArtChestCt, SetupCt, FixedCts } from "../../index";
import { Section, StyledSetter, StyledSelect } from "../CmnCpns";
import artifactDebuffs from "../../../../data/artifacts/artifactDebuffs";
import { elmtDebuffDesc, swirlables } from "../../../../data/elements";
import { deepCopy } from "../../../../helpers/misc";

export function ElmtDebuffs() {
  const elmtMCs = useContext(ElmtMCsCt);
  const curSetupI = useContext(SetupCt);
  const { setElmtMCs } = useContext(FixedCts);
  const { resonance, superconduct } = elmtMCs[curSetupI];
  const geoRsnI = resonance.findIndex((rsn) => rsn.name === "Enduring Rock");
  let geoRsnOn;
  if (geoRsnI >= 0) {
    geoRsnOn = resonance[geoRsnI].activated;
  }
  return (
    <>
      <Section
        checked={superconduct}
        handleCheck={() =>
          setElmtMCs((prev) => {
            const MCs = deepCopy(prev);
            MCs[curSetupI].superconduct = !superconduct;
            return MCs;
          })
        }
        heading="Superconduct"
        desc={elmtDebuffDesc.Superconduct}
      />
      {geoRsnI >= 0 && (
        <Section
          key="rock"
          checked={geoRsnOn}
          handleCheck={() =>
            setElmtMCs((prev) => {
              const MCs = deepCopy(prev);
              MCs[curSetupI].resonance[geoRsnI].activated = !geoRsnOn;
              return MCs;
            })
          }
          heading="Enduring Rock"
          desc={elmtDebuffDesc["Enduring Rock"]}
        />
      )}
    </>
  );
}

export function ArtDebuffs() {
  const artChest = useContext(ArtChestCt);
  const curSetupI = useContext(SetupCt);
  const { setArtChest } = useContext(FixedCts);
  const { activated, set, inputs, index } = artChest[curSetupI].subDCs[0];
  const debuff = artifactDebuffs[set][index];

  return (
    <Section
      checked={activated}
      handleCheck={() =>
        setArtChest((prev) => {
          const newArts = deepCopy(prev);
          newArts[curSetupI].subDCs[0].activated = !activated;
          return newArts;
        })
      }
      heading={set}
      desc={debuff.desc}
      setters={
        <StyledSetter>
          <p className="label">Element swirled</p>
          <StyledSelect
            value={inputs[0]}
            onChange={(e) =>
              setArtChest((prev) => {
                const newArts = deepCopy(prev);
                newArts[curSetupI].subDCs[0].inputs[0] = e.target.value;
                return newArts;
              })
            }
          >
            {swirlables.map((opt) => (
              <option key={opt}>{opt}</option>
            ))}
          </StyledSelect>
        </StyledSetter>
      }
    />
  );
}
