import { useContext } from "react";
import { SetDBCt } from "../../App";
import WeaponCard from "./WeaponCard";
import ArtifactCard from "./ArtifactCard";
import { deepCopy } from "../../helpers/misc";
import { processNumInp } from "../../helpers/processors";

export function MyWpCard({ weapon, mutable }) {
  const { setMyWps } = useContext(SetDBCt);
  return (
    <WeaponCard
      weapon={weapon}
      mutable={mutable}
      upgrade={(level) => {
        setMyWps((prev) => {
          const newWps = deepCopy(prev);
          newWps[
            newWps.findIndex((item) => item.ID === weapon.ID)
          ].level = level;
          return newWps;
        });
      }}
      refine={(rfm) => {
        setMyWps((prev) => {
          const newWps = deepCopy(prev);
          newWps[
            newWps.findIndex((item) => item.ID === weapon.ID)
          ].refinement = +rfm;
          return newWps;
        });
      }}
    />
  );
}

export function MyArtCard(props) {
  const { art } = props;
  const { setMyArts } = useContext(SetDBCt);
  const max = art.rarity === 4 ? 16 : 20;
  return (
    <ArtifactCard
      {...props}
      enhance={(e) =>
        setMyArts((prev) => {
          const newArts = deepCopy(prev);
          newArts[
            newArts.findIndex((item) => item.ID === art.ID)
          ].level = +e.target.value.substr(1);
          return newArts;
        })
      }
      up4={() => {
        setMyArts((prev) => {
          const newArts = deepCopy(prev);
          for (let piece of newArts) {
            if (piece.ID === art.ID) {
              piece.level = Math.min(max, piece.level + 4);
              return newArts;
            }
          }
        });
      }}
      toMax={() => {
        setMyArts((prev) => {
          const newArts = deepCopy(prev);
          newArts[newArts.findIndex((item) => item.ID === art.ID)].level = max;
          return newArts;
        });
      }}
      changeMSType={(e) =>
        setMyArts((prev) => {
          const newArts = deepCopy(prev);
          newArts[newArts.findIndex((item) => item.ID === art.ID)].mainSType =
            e.target.value;
          return newArts;
        })
      }
      changeSSType={(value, ssIndex) => {
        setMyArts((prev) => {
          const newArts = deepCopy(prev);
          for (let piece of newArts) {
            if (piece.ID === art.ID) {
              piece.subS[ssIndex].type = value;
              return newArts;
            }
          }
        });
      }}
      changeSSVal={(value, ssIndex, oldValue) => {
        setMyArts((prev) => {
          const newArts = deepCopy(prev);
          for (let piece of newArts) {
            if (piece.ID === art.ID) {
              piece.subS[ssIndex].val = processNumInp(value, oldValue, 9999);
              return newArts;
            }
          }
        });
      }}
    />
  );
}
