import { Collapse } from "@material-ui/core";
import { useState, useContext } from "react";
import { MyWpsCt, SetDBCt } from "../../App";
import { Box, AbsoluteBox } from "../../styledCpns/Layout";
import { IconBtn } from "../../styledCpns/Inputs";
import { StyledInv } from "../../styledCpns/Database";
import { ButtonBar } from "../CommonCpns";
import PrePicker from "../Picker/PrePicker";
import { WpPicker } from "../Picker/ItemPicker";
import { LeftSection, RightSection } from "./Sections";
import weapons from "../../data/weapons";
import { weaponIcons } from "../../data/misc";
import { deepCopy } from "../../helpers/misc";
import useTypeFilter from "../../helpers/hooks/useTypeFilter";

export default function MyWeapons() {
  const { setMyWps } = useContext(SetDBCt);
  const myWeapons = useContext(MyWpsCt);
  const [prePickerOn, setPrePickerOn] = useState(false);
  const [wpType, setWpType] = useState(null);
  const [chosenI, setChosenI] = useState(0);
  const [types, typeFilter] = useTypeFilter(true);

  const wps = types.length
    ? myWeapons.filter((wp) => types.includes(wp.type))
    : myWeapons;

  return (
    <Box className="pt-5 full-h flex-center" bgColor="cardBg2">
      <StyledInv>
        <BtnBar
          add={() => setPrePickerOn(true)}
          types={types}
          typeFilter={typeFilter}
        />
        <div className="body">
          <LeftSection wps={wps} chosenI={chosenI} setChosenI={setChosenI} />
          <RightSection weapon={wps[chosenI]} />
        </div>
      </StyledInv>
      {prePickerOn && (
        <PrePicker
          close={() => setPrePickerOn(false)}
          choices={weaponIcons}
          pick={(wpType) => {
            setWpType(wpType);
            setPrePickerOn(false);
          }}
        />
      )}
      {wpType && (
        <WpPicker
          needMassAdd={true}
          wpType={wpType}
          close={() => setWpType(null)}
          pick={(newItem) => {
            setMyWps((prev) => {
              const newWps = deepCopy(prev);
              newWps.unshift({ ID: Date.now(), user: null, ...newItem });
              return newWps;
            });
          }}
        />
      )}
    </Box>
  );
}

function BtnBar(props) {
  const { setMyWps } = useContext(SetDBCt);
  const [dropped, setDropped] = useState(false);
  return (
    <Box className="btn_bar full-w">
      <ButtonBar
        className="mr-3"
        distance="ml-3"
        texts={["Add", "Sort"]}
        variants={["positive", "positive"]}
        handlers={[
          props.add,
          () =>
            setMyWps((prev) => {
              const newWps = deepCopy(prev);
              newWps.sort((a, b) => sortWeapons(a, b));
              return newWps;
            })
        ]}
      />
      {window.innerWidth >= 500 ? (
        props.typeFilter
      ) : (
        <IconBtn
          className="ml-1"
          icon="fas fa-ellipsis-h"
          bgColor={dropped ? "green" : "white"}
          onClick={() => setDropped(!dropped)}
        />
      )}
      <AbsoluteBox className="full-w" top="100%" left={0} zIndex={2}>
        <Collapse in={dropped}>
          <Box className="px-3 py-4 cmn-shadow" bgColor="cardBg2">
            {props.typeFilter}
          </Box>
        </Collapse>
      </AbsoluteBox>
    </Box>
  );
}

function sortWeapons(a, b) {
  const rA = weapons[a.type][a.name].rarity;
  const rB = weapons[b.type][b.name].rarity;
  if (rA !== rB) return rB - rA;
  const [fA, sA] = a.level.split("/").map((lv) => +lv);
  const [fB, sB] = b.level.split("/").map((lv) => +lv);
  if (fA !== fB) {
    return fB - fA;
  }
  if (a.type !== b.type) {
    const type = {
      Bow: 5,
      Catalyst: 4,
      Polearm: 3,
      Claymore: 2,
      Sword: 1
    };
    return type[b.type] - type[a.type];
  }
  return sB - sA;
}
