import { useContext } from "react";
import { SelfMCsCt, PartiesCt, ResultCt, FixedCts, SetupCt } from "../../index";
import { Text } from "../../../../styledCpns/DataDisplay";
import { Section, NoContent, ModSetters } from "../CmnCpns";
import characters from "../../../../data/characters";
import { deepCopy } from "../../../../helpers/misc";
import { processNumInp } from "../../../../helpers/processors";

export function SelfBuffs({ char }) {
  const selfMCs = useContext(SelfMCsCt);
  const parties = useContext(PartiesCt);
  const { allATTRs } = useContext(ResultCt);
  const { setSelfMCs } = useContext(FixedCts);
  const curSetupI = useContext(SetupCt);
  const content = [];

  selfMCs[curSetupI].BCs.forEach((ctrl, bcIndex) => {
    const { activated, index, inputs } = ctrl;
    const buff = characters[char.name].buffs[index];
    if (buff.isGranted(char)) {
      content.push(
        <Section
          key={bcIndex}
          checked={activated}
          handleCheck={() => {
            setSelfMCs((prev) => {
              const MCs = deepCopy(prev);
              MCs[curSetupI].BCs[bcIndex].activated = !activated;
              return MCs;
            });
          }}
          heading={buff.src}
          desc={buff.desc({
            toSelf: true,
            ATTRs: allATTRs[curSetupI],
            char,
            charBCs: selfMCs[curSetupI].BCs,
            inputs,
            party: parties[curSetupI]
          })}
          setters={
            <SelfSetterSection
              buff={buff}
              inputs={inputs}
              setSelfMCs={setSelfMCs}
              curSetupI={curSetupI}
              bcIndex={bcIndex}
            />
          }
        />
      );
    }
  });
  return content.length ? content : <NoContent type="buffs" />;
}

function SelfSetterSection(props) {
  if (!props.buff.selfLabels) {
    return null;
  }
  const { buff, inputs, setSelfMCs, curSetupI, bcIndex } = props;
  return (
    <ModSetters
      labels={buff.selfLabels}
      mod={buff}
      inputs={inputs}
      handleText={(value, i) =>
        setSelfMCs((prev) => {
          const MCs = deepCopy(prev);
          MCs[curSetupI].BCs[bcIndex].inputs[i] = processNumInp(
            value,
            inputs[i],
            buff.maxs[i]
          );
          return MCs;
        })
      }
      handleCheck={(i) =>
        setSelfMCs((prev) => {
          const MCs = deepCopy(prev);
          MCs[curSetupI].BCs[bcIndex].inputs[i] = !inputs[i];
          return MCs;
        })
      }
      handleSelect={(value, i) =>
        setSelfMCs((prev) => {
          const MCs = deepCopy(prev);
          MCs[curSetupI].BCs[bcIndex].inputs[i] = isNaN(value) ? value : +value;
          return MCs;
        })
      }
    />
  );
}

export function PartyBuffs({ char }) {
  const parties = useContext(PartiesCt);
  const curSetupI = useContext(SetupCt);
  const content = [];

  parties[curSetupI].forEach((teammate, tmI) => {
    if (teammate.BCs.length)
      content.push(
        <TeammateBuffs
          key={tmI}
          teammate={teammate}
          curSetupI={curSetupI}
          tmI={tmI}
          char={char}
          party={parties[curSetupI]}
        />
      );
  });
  return content.length ? content : <NoContent type="buffs" />;
}

function TeammateBuffs(props) {
  const { setParties } = useContext(FixedCts);
  const { teammate, curSetupI, tmI, char } = props;
  const { buffs, vision } = characters[teammate.name];
  const subContent = [];

  teammate.BCs.forEach((ctrl, bcIndex) => {
    const { activated, index, inputs } = ctrl;
    const buff = buffs[index];
    subContent.push(
      <Section
        key={bcIndex}
        checked={activated}
        handleCheck={() => {
          setParties((prev) => {
            const pts = deepCopy(prev);
            pts[curSetupI][tmI].BCs[bcIndex].activated = !activated;
            return pts;
          });
        }}
        heading={buff.src}
        desc={buff.desc({
          toSelf: false,
          char,
          charBCs: teammate.BCs,
          inputs,
          party: props.party
        })}
        setters={
          <TmSetterSection
            buff={buff}
            inputs={inputs}
            setParties={setParties}
            curSetupI={curSetupI}
            tmI={tmI}
            bcIndex={bcIndex}
          />
        }
      />
    );
  });
  return (
    <>
      <Text
        className="pt-2 mb-m1"
        variant="h6"
        fontWeight={700}
        color={vision}
        align="center"
      >
        {teammate.name.toUpperCase()}
      </Text>
      {subContent}
    </>
  );
}

function TmSetterSection(props) {
  if (!props.buff.labels) {
    return null;
  }
  const { buff, inputs, setParties, curSetupI, tmI, bcIndex } = props;
  return (
    <ModSetters
      labels={buff.labels}
      mod={buff}
      inputs={inputs}
      handleText={(value, i) =>
        setParties((prev) => {
          const pts = deepCopy(prev);
          pts[curSetupI][tmI].BCs[bcIndex].inputs[i] = processNumInp(
            value,
            inputs[i],
            buff.maxs[i]
          );
          return pts;
        })
      }
      handleCheck={(i) =>
        setParties((prev) => {
          const pts = deepCopy(prev);
          pts[curSetupI][tmI].BCs[bcIndex].inputs[i] = !inputs[i];
          return pts;
        })
      }
      handleSelect={(value, i) =>
        setParties((prev) => {
          const pts = deepCopy(prev);
          pts[curSetupI][tmI].BCs[bcIndex].inputs[i] = isNaN(value)
            ? value
            : +value;
          return pts;
        })
      }
    />
  );
}
