import { useState, useContext, useEffect } from "react";
import { MyArtsCt, SetDBCt } from "../../App";
import { Text } from "../../styledCpns/DataDisplay";
import { Box, ModalCarpet } from "../../styledCpns/Layout";
import { ButtonBar } from "../CommonCpns";
import { deepCopy, initArtStatFilter } from "../../helpers/misc";
import { useCloseModal } from "../../helpers/hooks";
import useTypeFilter from "../../helpers/hooks/useTypeFilter";
import useArtStatsFilter from "../../helpers/hooks/useArtStatsFilter";
import useArtSetFilter from "../../helpers/hooks/useArtSetFilter";

export default function BtnBar(props) {
  const { filter } = props;
  const { setMyArts } = useContext(SetDBCt);
  const [filterOn, setFilterOn] = useState(false);
  const isFiltered =
    filter.types.length ||
    filter.sets.length ||
    filter.stats.main !== "All" ||
    filter.stats.subs.some((s) => s !== "All");

  return (
    <div className="btn_bar full-w">
      <ButtonBar
        className="mr-3"
        distance="ml-3"
        texts={["Add", "Sort"]}
        variants={["positive", "positive"]}
        handlers={[
          props.add,
          () =>
            setMyArts((prev) => {
              const newArts = deepCopy(prev);
              newArts.sort((a, b) => sortArtifacts(a, b));
              return newArts;
            })
        ]}
      />
      {window.innerWidth >= 600 && props.typeFilter}
      <Box className="flex pointer">
        <Box
          className={
            (isFiltered ? "pr-2" : "pr-3") + " pl-3 py-1 glow-on-hover"
          }
          bgColor="lightGold"
          borderRadius={isFiltered ? "1rem 0 0 1rem" : "1rem"}
          onClick={() => setFilterOn(true)}
        >
          <Text color="black" fontWeight={700}>
            Filter
          </Text>
        </Box>
        {isFiltered && (
          <Box
            className="pl-2 flex-center glow-on-hover"
            bgColor="red"
            borderRadius="0 1rem 1rem 0"
            style={{ paddingRight: "0.75rem" }}
            onClick={() => {
              filter.setTypes([]);
              filter.setSets([]);
              filter.setStats(initArtStatFilter());
            }}
          >
            <i className="fas fa-times" />
          </Box>
        )}
      </Box>
      {filterOn && <Filter filter={filter} close={() => setFilterOn(false)} />}
    </div>
  );
}

function sortArtifacts(a, b) {
  if (a.level !== b.level) {
    return b.level - a.level;
  }
  if (a.type !== b.type) {
    const type = {
      flower: 5,
      plume: 4,
      sands: 3,
      goblet: 2,
      circlet: 1
    };
    return type[b.type] - type[a.type];
  }
  return b.set.localeCompare(a.set);
}

function Filter(props) {
  const { filter, close } = props;
  const [err, setErr] = useState(false);
  const myArts = useContext(MyArtsCt);
  const [tempTypes, typeFilter] = useTypeFilter(false, filter.types);
  const [stats, statsFilter] = useArtStatsFilter(filter.stats, err);
  const [sets, setsFilter] = useArtSetFilter(myArts, filter.sets);
  const ref = useCloseModal(close);

  useEffect(() => setErr(false), [stats]);

  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="p-3 white-glow-shadow max-width-95"
        borderRadius="0.5rem"
        bgColor="cardBg3"
      >
        <div className="pb-2 flex custom-sb">
          <div>
            <div className="mt-2 mb-3">{typeFilter}</div>
            {statsFilter}
          </div>
          {setsFilter}
        </div>
        <ButtonBar
          className="mt-3"
          texts={["Cancel", "Confirm"]}
          variants={["negative", "positive"]}
          handlers={[
            close,
            () => {
              if (hasDupStat(stats)) {
                setErr(true);
                return;
              }
              filter.setTypes(tempTypes);
              filter.setSets(sets);
              filter.setStats(stats);
              close();
            }
          ]}
        />
      </Box>
    </ModalCarpet>
  );
}

// also used in ../Readers/ArtFilter
export function hasDupStat(stats) {
  const existed = [stats.main];
  for (let ss of stats.subs) {
    if (ss === "All") continue;
    if (existed.includes(ss)) return true;
    else existed.push(ss);
  }
  return false;
}
