import { useState, useContext, useEffect } from "react";
import { ResultCt, ManagerCts } from "../index";
import { Box, AbsoluteBox, ModalCarpet } from "../../../styledCpns/Layout";
import { IconBtn, Select } from "../../../styledCpns/Inputs";
import { Text } from "../../../styledCpns/DataDisplay";
import { DmgTable } from "../../CommonCpns/DmgTable";
import { Card } from "../styles";
import { OneSetup, MultiSetup } from "./Layouts";
import { getNamesForTables } from "../../../helpers/misc";
import { useCloseModal } from "../../../helpers/hooks";

export default function DmgResults({ char }) {
  const [enlarged, setEnlarged] = useState(false);
  if (!char) {
    return <Card bgColor="cardBg3" />;
  }
  const namesForTables = getNamesForTables(char);
  return (
    <Card className="px-3 pt-2 pb-4 flex-col relative" bgColor="cardBg3">
      {window.innerWidth >= 610 && (
        <AbsoluteBox top="0.75rem" left="0.75rem">
          <IconBtn
            icon="fas fa-expand-arrows-alt"
            className="glow-on-hover"
            variant="positive"
            size="1.75rem"
            onClick={() => setEnlarged(true)}
          />
        </AbsoluteBox>
      )}
      <Inner char={char} namesForTables={namesForTables} />
      {enlarged && (
        <EnlargedInner
          char={char}
          namesForTables={namesForTables}
          close={() => setEnlarged(false)}
        />
      )}
    </Card>
  );
}

function EnlargedInner(props) {
  const ref = useCloseModal(props.close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        height="90%"
        className="px-4 pt-2 pb-3 flex-col"
        borderRadius="0.5rem"
        bgColor="cardBg3"
      >
        <Inner char={props.char} namesForTables={props.namesForTables} />
      </Box>
    </ModalCarpet>
  );
}

function Inner(props) {
  const { char, namesForTables } = props;
  const { allDmg } = useContext(ResultCt);
  const { setups, stdSetupI, cmpSetupIs } = useContext(ManagerCts);
  const [focus, setFocus] = useState("Average");
  const [closed, setClosed] = useState([]);
  const charName = char ? char.name : "";
  useEffect(() => {
    setClosed([]);
  }, [charName]);

  return (
    <>
      {cmpSetupIs.length > 1 ? (
        <div className="mb-3 flex justify-center">
          <Text className="mr-2">Choose a focus</Text>
          <Select
            fontSize="1rem"
            align="center"
            color="lightGold"
            value={focus}
            onChange={(e) => setFocus(e.target.value)}
          >
            {["Non-crit", "Crit", "Average"].map((opt) => (
              <option key={opt}>{opt}</option>
            ))}
          </Select>
        </div>
      ) : (
        <Text className="mx-3 my-2" fontWeight={700} align="center">
          {setups[stdSetupI].name.toUpperCase()}
        </Text>
      )}
      <Box className="grow-1 hide-sb">
        {namesForTables.map((name, i) => {
          const stdValues = allDmg[stdSetupI][name.main];
          return (
            <DmgTable
              key={i}
              index={i}
              heading={name.main}
              closed={closed}
              setClosed={setClosed}
              mb={i === namesForTables.length - 1 ? "" : " mb-3"}
            >
              {cmpSetupIs.length > 1 ? (
                <MultiSetup name={name} stdValues={stdValues} focus={focus} />
              ) : (
                <OneSetup subNames={name.subs} stdValues={stdValues} />
              )}
            </DmgTable>
          );
        })}
      </Box>
    </>
  );
}
