import styled from "styled-components";
import { Collapse } from "@material-ui/core";
import { useState } from "react";
import { Text } from "../../styledCpns/DataDisplay";
import { IconBtn, Checkbox } from "../../styledCpns/Inputs";
import { AbsoluteBox, Box } from "../../styledCpns/Layout";
import { StyledHeading } from "../../styledCpns/Database";
import { CloseBtn } from "../CommonCpns";
import { artNames, wikia, visionIcons, weaponIcons } from "../../data/misc";

export default function Heading(props) {
  const [filterOn, setFilterOn] = useState(false);
  const { dataType, massAdd, needMassAdd } = props;
  return (
    <StyledHeading>
      {(window.innerWidth >= 769 || !needMassAdd) && (
        <p className="heading_text">{artNames[dataType] || dataType}</p>
      )}
      {dataType === "Character" && (
        <>
          <IconBtn
            icon="fas fa-filter"
            className={"filter_btn glow-on-hover" + (filterOn ? " on" : "")}
            onClick={() => setFilterOn(!filterOn)}
          />
          <AbsoluteBox className="full-w" top="100%" left={0} zIndex={3}>
            <Box
              borderRadius="0 0 0.5rem 0.5rem"
              bgColor="cardBg3"
              boxShadow="var(--cmn-shadow)"
            >
              <Collapse in={filterOn}>
                <CharFilter
                  setFilterOn={setFilterOn}
                  filter={props.filter}
                  setFilter={props.setFilter}
                />
              </Collapse>
            </Box>
          </AbsoluteBox>
        </>
      )}
      {needMassAdd && (
        <AbsoluteBox right="4rem" className="flex align-center">
          <Checkbox
            scale={1.6}
            checked={massAdd}
            onChange={() => props.setMassAdd(!massAdd)}
          />
          <Text className="mt-1 ml-2" fontWeight={700} color="black">
            Mass Add ({props.count})
          </Text>
        </AbsoluteBox>
      )}
      <CloseBtn className="close_btn" onClick={props.close} />
    </StyledHeading>
  );
}

function CharFilter({ filter, setFilter, setFilterOn }) {
  const { type, value } = filter;
  return (
    <StyledFilter>
      <div className="py-3 flex-wrap justify-around">
        <Box className="flex overflow-auto hide-sb">
          {Object.entries(visionIcons).map((pair, i) => {
            const chosen = type === "vision" && value === pair[0];
            return (
              <img
                key={i}
                className={"filter_icon vision" + (chosen ? " chosen" : "")}
                src={wikia + pair[1] + ".png"}
                alt="vision"
                draggable={false}
                onClick={() => {
                  setFilter(chosen, { type: "vision", value: pair[0] });
                  setFilterOn(false);
                }}
              />
            );
          })}
        </Box>
        <Box className="flex overflow-auto hide-sb">
          {Object.entries(weaponIcons).map((pair, i) => {
            const chosen = type === "weapon" && value === pair[0];
            return (
              <img
                key={i}
                className={"filter_icon weapon" + (chosen ? " chosen" : "")}
                src={wikia + pair[1] + ".png"}
                alt="vision"
                draggable={false}
                onClick={() => {
                  setFilter(chosen, { type: "weapon", value: pair[0] });
                  setFilterOn(false);
                }}
              />
            );
          })}
        </Box>
      </div>
    </StyledFilter>
  );
}

const StyledFilter = styled.div`
  padding: 0 0.75rem;
  .filter_icon {
    cursor: pointer;
    border-radius: 50%;
  }
  .filter_icon:not(:last-child) {
    margin-right: 1rem;
  }
  .filter_icon.chosen {
    border: 3px solid white;
  }
  .vision {
    width: 2rem;
    height: 2rem;
  }
  .weapon {
    width: 2.25rem;
    height: 2.25rem;
    margin-top: 1rem;
  }
  @media (min-width: 769px) {
    .weapon {
      margin-top: 0;
    }
  }
  @media (min-width: 1025px) {
    .vision {
      width: 2.5rem;
      height: 2.5rem;
    }
    .weapon {
      width: 2.75rem;
      height: 2.75rem;
    }
  }
`;
