import styled from "styled-components";
import { Box } from "../../../styledCpns/Layout";
import { Checkbox } from "../../../styledCpns/Inputs";
import { Text } from "../../../styledCpns/DataDisplay";
import { Section } from "../../Calculator/Modifiers/CmnCpns";
import { colorCodes } from "../../../data/misc";

export const Divider = styled.div`
  width: 2px;
  height: 100%;
  background-color: ${colorCodes.cardBg3};
  margin: 0 1rem;
`;

export const LinkIcon = styled.button`
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  color: white;
  font-size: 1rem;
  &.link {
    background-color: transparent;
  }
  &.unlink {
    background-color: ${colorCodes.red};
  }
`;

export const MemberWrapper = styled.div`
  position: relative;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  &.clickable {
    box-shadow: 0 0 3px 3px ${colorCodes.lightGold};
    cursor: pointer;
  }
  :not(:last-child) {
    margin-right: 1rem;
  }
  .calc-btn {
    position: absolute;
    bottom: -0.25rem;
    right: -0.25rem;
    display: none;
  }
  :hover .calc-btn {
    display: block;
  }
`;

export const StatWrapper = styled.div`
  width: 20rem;
  min-width: 20rem;
  display: flex;
  flex-direction: column;
  padding: 0.5rem 1rem 1rem;
  &:not(:first-child) {
    border-left: 2px solid ${colorCodes.cardBg3};
  }
`;

export const ModWrapper = (props) => {
  return (
    <Box className={"py-3 flex-col " + props.padding}>
      <Text
        className="mb-2"
        variant="h5"
        fontWeight={700}
        align="center"
        color="orange"
      >
        {props.type}
      </Text>
      <Box
        className="full-h custom-sb"
        width={
          props.type === "TARGET"
            ? window.innerWidth <= 1140
              ? "16.5rem"
              : "18.5rem"
            : window.innerWidth <= 1140
            ? "18.75rem"
            : "20.75rem"
        }
      >
        {props.children}
      </Box>
    </Box>
  );
};

export function ModReader(props) {
  return (
    <Section
      immutable={true}
      heading={"+ " + props.heading}
      desc={props.desc}
      setters={
        <SetterSection
          labels={props.labels}
          inputTypes={props.inputTypes}
          inputs={props.inputs}
        />
      }
    />
  );
}

function SetterSection(props) {
  if (!props.labels) {
    return null;
  }
  return props.labels.map((label, i) => {
    const isCheck = props.inputTypes[i] === "check";
    return (
      <div key={i} className="mt-1 flex justify-end align-center">
        <Text className={isCheck ? "mr-3" : "mr-2"} align="right">
          {label}
        </Text>
        {isCheck ? (
          <Checkbox className="mr-1" checked={true} readOnly />
        ) : (
          <Text fontWeight={700} color="orange">
            {props.inputs[i]}
          </Text>
        )}
      </div>
    );
  });
}
