import styled from "styled-components";
import { Collapse } from "@material-ui/core";
import { Text, LGreen, ColoredSpan } from "../../styledCpns/DataDisplay";
import { colorCodes, statsTypes } from "../../data/misc";
import { getRxnBnesFromEM } from "../../calculators/helpers";
import { useState } from "react";

// also used in ./TalentList/DetailsContent
export const Table = styled.div`
  width: 100%;
  cursor: default;
  .row {
    display: flex;
    justify-content: space-between;
    padding: 0.25rem 0.5rem 0;
    font-weight: 700;
  }
  .row:hover {
    background-color: #8a1200;
  }
  & > div:nth-child(2n + 1) {
    background-color: ${colorCodes.cardBg2};
  }
  .brokedown {
    display: none;
    white-space: nowrap;
  }
  .wrapper:hover .brokedown {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
  }
  .wrapper:hover .total {
    display: none;
  }
`;

export default function StatsTable(props) {
  const { stats, breakdown } = props;
  const EM = stats["Elemental Mastery"] || 0;
  const rxnBnFromEM = getRxnBnesFromEM(EM);
  return (
    <div>
      <Table>
        {["HP", "ATK", "DEF"].map((type, i) => (
          <div key={i} className="row wrapper">
            <Text>{type}</Text>
            <div className="relative">
              <Text className={"mr-2" + (breakdown ? " total" : "")}>
                {stats[type]}
              </Text>
              {breakdown && (
                <Text className="mr-2 brokedown">
                  {stats["Base " + type]} +{" "}
                  <LGreen>
                    {(stats[type] || 0) - (stats["Base " + type] || 0)}
                  </LGreen>
                </Text>
              )}
            </div>
          </div>
        ))}
        <Droppable EM={EM} rxnBnFromEM={rxnBnFromEM} />
        {statsTypes.slice(4).map((type, i) => (
          <div key={i} className="row">
            <Text>{type}</Text>
            <Text className="mr-2">
              {Math.round((stats[type] || 0) * 10) / 10}%
            </Text>
          </div>
        ))}
      </Table>
    </div>
  );
}

function Droppable({ EM, rxnBnFromEM }) {
  const [dropped, setDropped] = useState(false);
  return (
    <div>
      <div className="row pointer" onClick={() => setDropped(!dropped)}>
        <Text>
          Elemental Mastery{" "}
          <ColoredSpan className="ml-1" color={dropped ? "green" : "white"}>
            <i className={"fas fa-caret-" + (dropped ? "up" : "down")} />
          </ColoredSpan>
        </Text>
        <Text className="mr-2">{EM}</Text>
      </div>
      <Collapse in={dropped}>
        <div className="px-2 py-1">
          <Text variant="subtitle1">
            Increases damage caused by Vaporize and Melt by{" "}
            <LGreen>{rxnBnFromEM.amplifying}%</LGreen>.
          </Text>
          <Text className="mt-1" variant="subtitle1">
            Increases damage caused by Overloaded, Superconduct,
            Electro-Charged, Shattered, and Swirl by{" "}
            <LGreen>{rxnBnFromEM.transformative}%</LGreen>.
          </Text>
          <Text className="mt-1" variant="subtitle1">
            Increases damage absorption power of shields created through
            Crystallize by <LGreen>{rxnBnFromEM.shield}%</LGreen>.
          </Text>
        </div>
      </Collapse>
    </div>
  );
}
