import { useContext, useState } from "react";
import { MyCharsCt, SetDBCt } from "../../App";
import { Box } from "../../styledCpns/Layout";
import { StyledRack, Card } from "../../styledCpns/Database";
import { ButtonBar, EquippedChar, Confirmation, NoItems } from "../CommonCpns";
import Thumb from "../Readers/Thumb";
import { MyWpCard } from "../Readers/MyItemCard";
import Picker from "../Picker";
import { initWeapon } from "../Picker/ItemPicker";
import characters from "../../data/characters";
import weapons from "../../data/weapons";
import { deepCopy } from "../../helpers/misc";
import { useGetHeight } from "../../helpers/hooks";

export function LeftSection(props) {
  const [clickedI, setClickedI] = useState(null);
  return (
    <StyledRack className="hide-sb">
      {props.wps.length ? (
        <div className="flex-wrap">
          {props.wps.map((weapon, i) => {
            const { icon, rarity } = weapons[weapon.type][weapon.name];
            return (
              <div key={i} className="cell relative">
                <Thumb
                  item={weapon}
                  icon={icon}
                  rarity={rarity}
                  chosen={i === props.chosenI}
                  clicked={i === clickedI}
                  handleMouseDown={() => setClickedI(i)}
                  choose={() => {
                    setClickedI(null);
                    props.setChosenI(i);
                  }}
                />
              </div>
            );
          })}
        </div>
      ) : (
        <NoItems type="Weapons" />
      )}
    </StyledRack>
  );
}

export function RightSection(props) {
  const { weapon } = props;
  const { setMyChars, setMyWps } = useContext(SetDBCt);
  const myChars = useContext(MyCharsCt);
  const [pickerOn, setPickerOn] = useState(null);
  const [removing, setRemoving] = useState(false);
  const [ref, height] = useGetHeight();

  const swap = (name) => {
    if (!weapon) return;
    const { ID, user } = weapon;
    const targetCharI = myChars.findIndex((char) => char.name === name);
    setMyWps((prev) => {
      const wps = deepCopy(prev);
      const wpI_new = wps.findIndex((item) => item.ID === ID);
      wps[wpI_new].user = name;
      const { weaponID } = myChars[targetCharI];
      if (weaponID) {
        const wpI_old = wps.findIndex((item) => item.ID === weaponID);
        wps[wpI_old].user = user;
      }
      return wps;
    });
    setMyChars((prev) => {
      const chars = deepCopy(prev);
      if (user) {
        const currentCharI = chars.findIndex((char) => char.name === user);
        chars[currentCharI].weaponID = chars[targetCharI].weaponID;
      }
      chars[targetCharI].weaponID = ID;
      return chars;
    });
  };

  return (
    <div ref={ref} className="full-h flex-col">
      <div className="grow-1 flex align-start">
        <Card
          className="flex-col"
          style={{ minHeight: "27rem", maxHeight: height / 16 - 3 + "rem" }}
        >
          <Box className="grow-1 hide-sb" width="18.75rem">
            {weapon ? <MyWpCard weapon={weapon} mutable={true} /> : null}
          </Box>
          {weapon ? (
            <ButtonBar
              className="mt-3"
              texts={["Remove", "Equip"]}
              handlers={[() => setRemoving(true), () => setPickerOn(true)]}
            />
          ) : null}
        </Card>
      </div>
      {weapon?.user ? <EquippedChar user={weapon.user} /> : null}
      {pickerOn && (
        <CharPicker
          weapon={weapon}
          myChars={myChars}
          swap={swap}
          close={() => setPickerOn(false)}
        />
      )}
      {removing && (
        <Confirmation
          message={
            <>
              Remove "<b>{weapon.name}</b>
              "?{" "}
              {Boolean(weapon.user) && (
                <>
                  It is currently used by <b>{weapon.user}</b>.
                </>
              )}
            </>
          }
          close={() => setRemoving(false)}
          confirm={() => {
            const { user } = weapon;
            const newWpID = Date.now();
            setMyWps((prev) => {
              const wps = deepCopy(prev);
              const index = wps.findIndex((wp) => wp.ID === weapon.ID);
              wps.splice(index, 1);
              if (user) {
                wps.push({
                  ID: newWpID,
                  user,
                  ...initWeapon({ type: weapon.type })
                });
              }
              return wps;
            });
            if (user) {
              setMyChars((prev) => {
                const chars = deepCopy(prev);
                chars[
                  chars.findIndex((char) => char.name === user)
                ].weaponID = newWpID;
                return chars;
              });
            }
          }}
        />
      )}
    </div>
  );
}

function CharPicker(props) {
  const { type, user } = props.weapon;
  const data = [];
  for (let { name, constellation } of props.myChars) {
    if (characters[name].weapon === type && name !== user) {
      const { icon, rarity, vision, weapon } = characters[name];
      data.push({ name, icon, rarity, vision, weapon, constellation });
    }
  }
  return (
    <Picker
      data={data}
      dataType="Character"
      close={props.close}
      pick={({ name }) => props.swap(name)}
    />
  );
}
